<template>
  <div>
    <nav class="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
      <a class="navbar-brand font-weight-bold col-sm-3 col-md-2 mr-0 " href="#">監測系統</a>
      <ul class="navbar-nav px-3">
        <li class="nav-item text-nowrap">
          <router-link to="/" class="nav-link">離開監測系統</router-link>
        </li>
      </ul>
    </nav>
    <div class="container-fluid">
      <div class="row">
        <main role="main" class="col-12 px-4">
          <div class="mt-md-5 mt-0 mx-auto col-12 col-lg-8">
            <form @submit.prevent>
              <label for="patient-data" class="h6">請選擇欲監測病患</label>
              <select class="custom-select" id="patient-data" @change="getPatientData"
                v-model="chosenPatient">
                <option :value="id" v-for="(patient, id) in patients" :key="id" :id="patient">
                  編號:{{id + 1}} | 姓名:王小{{id + 1}} | 組別:{{patient}}
                </option>
              </select>
            </form>
            <div class="onoffswitch mt-3">
              <input type="checkbox" name="onoffswitch" class="onoffswitch-checkbox"
                id="myonoffswitch">
              <label class="onoffswitch-label" for="myonoffswitch" @click="isPatientExistJudge">
                <span class="onoffswitch-inner"></span>
                <span class="onoffswitch-switch"></span>
              </label>
            </div>
          </div>
            <div v-if="fakePatientInfo" class="mt-0 mx-auto col-12 col-lg-8">
              <div class="table-responsive">
                <table class="table table-striped table-sm">
                  <thead>
                    <tr>
                      <th>編號</th>
                      <th>相片</th>
                      <th>年齡</th>
                      <th>性別</th>
                      <th>電話</th>
                    </tr>
                  </thead>
                  <tbody>
                    <tr>
                      <td>{{chosenPatient + 1}}</td>
                      <td>
                        <img :src="fakePatientInfo.picture.large" alt="fake">
                      </td>
                      <td>{{fakePatientInfo.dob.age}}</td>
                      <td>{{fakePatientInfo.gender}}</td>
                      <td>{{fakePatientInfo.cell}}</td>
                    </tr>
                  </tbody>
                </table>
              </div>
            </div>
            <!-- line-chart -->
            <div class="col-lg-8 col-12 mx-auto">
              <canvas class="my-4 w-100" id="line-chart"></canvas>
            </div>
            <div class="col-lg-8 col-12 mx-auto" id="pressure-map-container">
              <div id="pressure-map" class="c-pressure-map d-flex justify-content-center"></div>
            </div>
        </main>
      </div>
    </div>
  </div>
</template>
<script>
import Chart from 'chart.js';
import d3 from 'd3';

/* eslint-disable */
export default {
  data() {
    return {
      isPatientExist: false,
      PatientExistData: 0,
      user: {
        email: '',
        patientQty: '',
      },
      patientGroup: {
        all: ['group1', 'group1', 'group1', 'group1', 'group1', 'group1', 'group2', 'group2', 'group2', 'group2', 'group2', 'group2'],
        group1: ['group1', 'group1', 'group1', 'group1', 'group1', 'group1'],
        group2: ['group2', 'group2', 'group2', 'group2', 'group2', 'group2'],
      },
      patients: [],
      userView: [],
      fakePatientInfo: null,
      chosenPatient: '',
      isMapExist: false,
      chart: {
        xAxis: [],
        datas: [],
      },
    };
  },
  methods: {
    getPatientData() { // 抓假資料
      const vm = this;
      // pressureMap function
      function pressureMapData() {
        const pressureMapAry = [];
        let xpos = 1;
        let ypos = 1;
        const width = 36;
        const height = 36;
        for (let rowLine = 0; rowLine < 16; rowLine++) {
          pressureMapAry.push([]);
          for (let colLine = 0; colLine < 4; colLine++) {
            pressureMapAry[rowLine].push({
              x: xpos,
              y: ypos,
              width: width,
              height: height,
            });
            xpos += width;
          }
          xpos = 1;
          ypos += height;
        }
        return pressureMapAry;
      }
      function getPressureMapStatus() { // 取得壓力圖資料
        const rectAll = document.querySelectorAll('rect');
        rectAll.forEach((rect) => {
          if (vm.isPatientExist) {
            rect.dataset.pressure = Math.floor(Math.random() * 100);
            if (rect.dataset.pressure >= 75) {
              rect.style.fill = 'rgba(255, 0, 0, 0.1)';
            } else if (rect.dataset.pressure >= 50 && rect.dataset.pressure <= 75) {
              rect.style.fill = 'rgba(255, 165, 0, 0.2)';
            } else if (rect.dataset.pressure >= 25 && rect.dataset.pressure <= 50) {
              rect.style.fill = 'rgba(0, 0, 255, 0.1)';
            } else {
              rect.style.fill = 'rgba(0, 128, 0, 0.1)';
            }
          } else {
            rect.style.fill = 'transparent';
          }
        });
      }
      // chart-part
      const ctx = document.getElementById('line-chart');
      const xAxis = vm.chart.xAxis;
      const datas = vm.chart.datas;
      const chart = new Chart(ctx, { // chart本體
        type: 'line',
        data: {
          labels: xAxis,
          datasets: [
            {
              label: '是否在床 (2表示在床)',
              data: datas,
              backgroundColor: 'rgba(0, 123, 255, 0.5)',
            },
          ],
        },
        responsive: true,
        options: {
          scales: {
            yAxes: [
              {
                ticks: {
                  suggestedMin: -1,
                  suggestedMax: 2,
                  beginAtZero: true,
                  stepSize: 1,
                },
                scaleLabel: {
                  display: true,
                  labelString: '監測指數',
                },
              },
            ],
          },
        },
      });
      // pressureMap part
      const pressureMap = d3.select('#pressure-map')
        .append('svg')
        .attr({
          width: `${36 * 16 + 2}px`,
          height: `${36 * 16 + 2}px`,
        })
        .attr('class', 'pressure-map-svg');
      const row = pressureMap.selectAll('.row')
        .data(pressureMapData)
        .enter()
        .append('g')
        .attr('class', 'row');
      row.selectAll('.square')
        .data(d => d)
        .enter().append('rect')
        .attr('class', 'square')
        .attr('x', d => d.x)
        .attr('y', d => d.y)
        .attr('width', d => d.width)
        .attr('height', d => d.height)
        .style('fill', 'transparent')
        .style('stroke', 'rgba(0, 0, 0, 0.05)');
      // chart function
      function appendXAxis() { // 移除 chart 前面的資料表現
        const date = new Date();
        
        if (xAxis.length > 10) {
          xAxis.shift();
          datas.shift();
        }
        xAxis.push( // 取得現在時間 並加入 chart 的 x軸
          `${date.getHours() < 10 ? '0' + date.getHours() : date.getHours()}:${
            date.getMinutes() < 10 ? '0' + date.getMinutes() : date.getMinutes()
          }:${date.getSeconds() < 10 ? '0' + date.getSeconds() : date.getSeconds()}`,
        );
      }
      function appendDatas() { // 增加 chart 資料
        datas.push(vm.isPatientExist);
      }
      function updateChart() { // 持續更新 chart 資料
        appendXAxis();
        appendDatas();
        chart.update();
      }
      // 動態更新資料
      const reUpdateChart = setInterval(() => updateChart(), 1000);
      const reUpdatePressureMapStatus = setInterval(() => getPressureMapStatus(), 1750);
      // get api
      const url = 'https://randomuser.me/api/';
      const elPressureMap = document.getElementById('pressure-map');
      const xhr = new XMLHttpRequest();
      let info;
      vm.$store.dispatch('loadingOverlay');
      xhr.open('get', url);
      xhr.send(null);
      xhr.onloadend = () => {
        vm.chart.xAxis = [];
        vm.chart.datas = [];
        info = JSON.parse(xhr.response);
        vm.fakePatientInfo = { ...info.results[0] };
        vm.$store.dispatch('loadingOverlay');
        if (vm.isMapExist) {
          clearInterval(reUpdatePressureMapStatus);
          clearInterval(reUpdateChart);
        }
        reUpdateChart;
        if (vm.isMapExist) {
          elPressureMap.removeChild(elPressureMap.childNodes[0]);
        }
        vm.isMapExist = true;
      };
    },
    isPatientExistJudge() { // 判斷病患是否在床
      const vm = this;
      vm.isPatientExist = !vm.isPatientExist;
      vm.isPatientExist ? vm.PatientExistData = 1 : vm.PatientExistData = 0;
    },
    // fake data
    getUserInfo() { // 從 localStorage 拿假資料
      const vm = this;
      vm.user.email = JSON.parse(localStorage.getItem('email')).email;
      vm.user.patientQty = JSON.parse(localStorage.getItem('patientQty')).patientQty;
    },
    setUserView() { // 根據 localStorage 的假資料判斷顯示數量
      const vm = this;
      if (vm.user.patientQty === 'part') {
        vm.patients = [...vm.patientGroup.group1];
      } else if (vm.user.patientQty === 'all') {
        vm.patients = [...vm.patientGroup.all];
      }
    },
  },
  created() {
    this.getUserInfo(); // 取得假資料
  },
  mounted() {
    this.setUserView(); // 根據假資料渲染畫面
  },
};
</script>

<style lang="scss">
@import "~@/scss/all.scss";
</style>
